var React = require("react"),
    Router = require("react-router"),
    RouteHandler = Router.RouteHandler,
    Link = Router.Link,
    Fluxxor = require("../../../../");

var RecipeList = React.createClass({displayName: 'RecipeList',
  mixins: [Fluxxor.FluxMixin(React), Fluxxor.StoreWatchMixin("recipe")],

  getStateFromFlux: function() {
    return {
      recipes: this.getFlux().store("recipe").getRecipes()
    };
  },

  render: function() {
    return (
      React.createElement("div", null, 
        React.createElement("h1", null, "Recipes"), 
        React.createElement("ul", null, this.state.recipes.map(this.renderRecipeLink)), 
        React.createElement("div", null, 
          React.createElement(Link, {to: "add-recipe"}, "Add New Recipe")
        )
      )
    );
  },

  renderRecipeLink: function(recipe) {
    return (
      React.createElement("li", {key: recipe.id}, 
        React.createElement(Link, {to: "recipe", params: {id: recipe.id}}, recipe.name)
      )
    );
  }
});

module.exports = RecipeList;
