var React = require("react"),
    Router = require("react-router"),
    Link = Router.Link,
    State = Router.State,
    Fluxxor = require("../../../../");

var RecipeStore = require("../recipe_store.jsx");

var Recipe = React.createClass({displayName: 'Recipe',
  mixins: [
    Fluxxor.FluxMixin(React),
    Fluxxor.StoreWatchMixin("recipe"),
    State
  ],

  getStateFromFlux: function() {
    var params = this.getParams();

    return {
      recipe: this.getFlux().store("recipe").getRecipe(params.id)
    };
  },

  componentWillReceiveProps: function(nextProps) {
    this.setState(this.getStateFromFlux());
  },

  render: function() {
    var recipe = this.state.recipe;

    if (recipe === RecipeStore.NOT_FOUND_TOKEN) {
      return this.renderNotFound();
    }

    var description = (recipe.description || "").replace(/\n/g, "<br>"),
        directions = (recipe.directions || "").replace(/\n/g, "<br>");

    return this.renderWithLayout(
      React.createElement("div", null, 
        React.createElement("h1", null, recipe.name), 
        React.createElement("p", {dangerouslySetInnerHTML: {__html: description}}), 
        React.createElement("strong", null, "Ingredients:"), 
        React.createElement("ul", null, recipe.ingredients.map(this.renderIngredient)), 
        React.createElement("strong", null, "Directions:"), 
        React.createElement("p", {dangerouslySetInnerHTML: {__html: directions}}), 

        React.createElement("p", null, 
          React.createElement(Link, {to: "edit-recipe", params: {id: recipe.id}}, "Edit Recipe"), 
          " | ", React.createElement(Link, {to: "home", onClick: this.deleteRecipe}, "Delete Recipe")
        )
      )
    );
  },

  renderIngredient: function(ingredient, idx) {
    return (
      React.createElement("li", {key: idx}, 
        React.createElement("strong", null, ingredient.quantity), " ", ingredient.item
      )
    );
  },

  renderNotFound: function() {
    return this.renderWithLayout(
      React.createElement("div", null, "That recipe was not found.")
    );
  },

  renderWithLayout: function(content) {
    return (
      React.createElement("div", null, 
        content, 
        React.createElement("hr", null), 
        React.createElement(Link, {to: "home"}, "Home"), 
        " | ", React.createElement(Link, {to: "add-recipe"}, "Add New Recipe")
      )
    );
  },

  deleteRecipe: function(e) {
    if (confirm("Really delete this recipe?")) {
      this.getFlux().actions.recipes.remove(this.state.recipe.id);
    } else {
      e.preventDefault();
    }
  }
});

module.exports = Recipe;
