var React = require("react"),
    t = require("tcomb-form");

var Recipe = require("../schemas/recipe.jsx");

var ingredients = function(locals) {
  var inputs = locals.inputs;

  return (
    React.createElement("fieldset", {className: "form-struct ingredient"}, 
      React.createElement("div", {className: "form-row quantity"}, 
        inputs.quantity
      ), 
      React.createElement("div", {className: "form-row item"}, 
        inputs.item
      )
    )
  );
};

var struct = function(locals) {
  if (locals.config.type === "ingredients") return ingredients(locals);

  var inputs = locals.inputs;
  console.log(locals);

  return (
    React.createElement("fieldset", {className: "form-struct recipe"}, 
      React.createElement("div", {className: "form-row name"}, 
        inputs.name
      ), 
      React.createElement("div", {className: "form-row description"}, 
        inputs.description
      ), 
      React.createElement("div", {className: "form-row ingredients"}, 
        React.createElement("label", null, 
          React.createElement("strong", null, "Ingredients")
        ), 
        inputs.ingredients
      ), 
      React.createElement("div", {className: "form-row directions"}, 
        inputs.directions
      )
    )
  );

  return (
    React.createElement("fieldset", {className: "form-struct"}, 
    
      locals.order.map(function(field) {
        return (
          React.createElement("div", {key: field, className: "form-row " + field}, 
            locals.inputs[field]
          )
        );
      })
    
    )
  );
};

var list = function(locals) {
  return (
    React.createElement("fieldset", {className: "form-list"}, 
    
      locals.items.map(function(item) {
        return (
          React.createElement("div", {key: item.key}, 
            item.input, 
            React.createElement("div", {className: "form-list-buttons"}, 
            item.buttons.map(function(btn) {
              return (
                React.createElement("button", {key: btn.label, onClick: btn.click}, btn.label)
              );
            })
            )
          )
        );
      }), 
    
    React.createElement("div", {className: "form-list-add-button"}, 
      React.createElement("button", {onClick: locals.add.click}, locals.add.label)
    )
    )
  );
};

module.exports = t.form.create(Recipe, {
  templates: {
    struct: struct,
    list: list
  },
  fields: {
    description: {
      type: "textarea"
    },
    directions: {
      type: "textarea"
    },
    ingredients: {
      config: { type: "ingredients" }
    }
  }
});
