var React = require("react"),
    Router = require("react-router"),
    RouteHandler = Router.RouteHandler,
    Link = Router.Link,
    Navigation = Router.Navigation,
    State = Router.State,
    Fluxxor = require("../../../../");

var RecipeForm = require("../forms/recipe_form.jsx");

var RecipeAdder = React.createClass({displayName: 'RecipeAdder',
  mixins: [
    Fluxxor.FluxMixin(React),
    State,
    Navigation
  ],

  render: function() {
    return this.renderWithLayout(
      React.createElement("div", null, 
        React.createElement("form", {onSubmit: this.onSubmit}, 
          React.createElement(RecipeForm, {ref: "form"}), 
          React.createElement("input", {type: "submit", value: "Save"})
        )
      )
    );
  },

  renderWithLayout: function(content) {
    return (
      React.createElement("div", null, 
        content, 
        React.createElement("hr", null), 
        React.createElement(Link, {to: "home"}, "Home"), 
        " | ", React.createElement(Link, {to: "add-recipe"}, "Add New Recipe")
      )
    );
  },

  onSubmit: function(e) {
    e.preventDefault();

    var newRecipe = this.refs.form.getValue();
    if (newRecipe) {
      this.getFlux().actions.recipes.add(
        newRecipe.name,
        newRecipe.description,
        newRecipe.ingredients,
        newRecipe.directions,
        function(recipe) {
          this.transitionTo("recipe", {id: recipe.id});
        }.bind(this)
      );
    }
  },

  deleteRecipe: function(e) {
    if (confirm("Really delete this recipe?")) {
      this.getFlux().actions.recipes.remove(this.state.recipe.id);
    } else {
      e.preventDefault();
    }
  }
});

module.exports = RecipeAdder;
