var React = require("react"),
    Router = require("react-router"),
    Route = Router.Route,
    DefaultRoute = Router.DefaultRoute,
    RouteHandler = Router.RouteHandler;

var Recipe = require("./components/recipe.jsx"),
    RecipeEditor = require("./components/recipe_editor.jsx"),
    RecipeAdder = require("./components/recipe_adder.jsx"),
    RecipeList = require("./components/recipe_list.jsx");

var routes = (
  React.createElement(Route, {handler: RouteHandler, name: "home", path: "/"}, 
    React.createElement(Route, {handler: RecipeAdder, name: "add-recipe", path: "/recipe/add"}), 

    React.createElement(Route, {handler: RouteHandler, path: "/recipe/:id"}, 
      React.createElement(Route, {handler: RecipeEditor, name: "edit-recipe", path: "edit"}), 
      React.createElement(DefaultRoute, {handler: Recipe, name: "recipe"})
    ), 

    React.createElement(DefaultRoute, {handler: RecipeList})
  )
);

module.exports = routes;
